@use './helpers/abstracts' as *;

.header {
	position: fixed;
	top: 0;
	width: 100%;
	background: $color_white;
	z-index: $zindex_header;
	// overflow: hidden;
}

.header__top {
	position: relative;
	display: flex;
	justify-content: space-between;
	transition: height $transition_dur ease-in-out;
	@include bp($point_2, min) {
		justify-content: space-between;
	}
	@include bp($point_4, min) {
		height: 4.6rem;
		align-items: center;
	}
	@include bp($point_5) {
		height: 7.6rem;
		align-items: end;
	}

	&::before {
		position: absolute;
		top: 0;
		left: 50%;
		z-index: -1;
		transform: translateX(-50%);

		content: '';

		width: 100dvw;
		height: 100%;

		background-color: $color_accent;
	}

	.header.header--scroll & {
		height: 0;
	}
}

.header__phone_icon_w {
	display: block;
	height: 2rem;
	width: 2rem;
}

.header__location_icon,
.header__phone_icon {
	@extend %contain_img;
}

.header__phone_link {
	text-decoration: none;
	font-weight: 700;
	color: $color_white;
	@include bp($point_4, min) {
		font-size: 2.2rem;
	}
	@include bp($point_5) {
		font-size: 1.6rem;
	}
}

.header__location_icon_w {
	width: 1.4rem;
	height: 2rem;
}
.header__location {
	display: flex;
	align-items: center;
	column-gap: 1.4rem;
	font-size: 1.6rem;
	font-weight: 400;
	color: $color_white;

	@include bp($point_3) {
		display: none;
	}
}
.header__bottom {
	position: relative;
	height: 10.2rem;
	transition: all $transition_dur ease-in-out;

	&::before {
		position: absolute;
		top: 0;
		left: 50%;
		z-index: -1;
		transform: translateX(-50%);

		content: '';

		width: 100dvw;
		height: 100%;

		box-shadow: 0px 4px 2rem 0px $color_shadow;
	}

	.header.header--scroll & {
		height: 6rem;
	}
}

.header__phone_list {
	display: flex;
	@include bp($point_4, min) {
		column-gap: 2rem;
	}
	@include bp($point_5) {
		column-gap: 1.2rem;
	}
}

.header__phone_list,
.header__location {
	transition: opacity $transition_dur ease-in-out;
	.header.header--scroll & {
		opacity: 0;
	}
}

.header_nav {
	display: flex;
	justify-content: space-between;
	align-items: center;
	column-gap: 9rem;
	height: 100%;

	@include bp($point_3) {
		display: none;

		&.active {
			display: block;
		}
	}
}

.header_nav__in {
	display: flex;
	justify-content: space-between;
	column-gap: 5rem;
	flex: 1 0 auto;
	height: 100%;
	@include bp($point_3) {
		display: none;
	}
}

.header_nav__link {
	color: $color_title;
	text-decoration: none;
	font-family: $font_2;
	line-height: 10.2rem;
	font-size: 2.2rem;
	font-weight: 700;
	transition: all $transition_dur ease-in-out;
	&:hover,
	&:active,
	&:focus {
		color: $color_accent;
	}
	.header.header--scroll & {
		line-height: 6rem;
	}
}
.header_nav__item {
	position: relative;
	height: 100%;
}

.header_dropdown {
	position: absolute;
	left: 50%;
	transform: translate(-50%);
	box-shadow: 0 -5px $color_accent;
}

.header_dropdown__item {
	position: relative;
	width: 22rem;
	padding: 2.8rem 0;
	background-color: $color_white;
	transition: all $transition_dur ease-in-out;

	&:hover {
		background-color: $color_header_dropdown_item;
	}
}

.header_dropdown__link {
	display: block;
	font-size: 2rem;
	font-weight: 400;
	text-align: center;
	text-decoration: none;
	color: $color_descr_1;
	transition: all $transition_dur ease-in-out;
	&:hover {
		color: $color_text_2;
	}
}

.header_dropdown--lev_1 {
	visibility: hidden;
	opacity: 0;
	transition: all $transition_dur ease-in-out;
}
.header_nav__item:hover > .header_dropdown--lev_1 {
	visibility: visible;
	opacity: 1;
}
.header_dropdown--lev_2 {
	visibility: hidden;
	opacity: 0;
	transition: all $transition_dur ease-in-out;
	left: 150%;
	top: 0;
}
.header_dropdown__item:hover > .header_dropdown--lev_2 {
	visibility: visible;
	opacity: 1;
}

.menu_icon {
	position: absolute;
	right: 2rem;
	top: 50%;
	transform: translateY(-50%);
	width: 20px;
	height: 14px;
	cursor: pointer;

	@include bp($point_2, min) {
		display: none;
	}
}

.menu_icon span {
	background-color: $color_title;
	height: 2px;
	position: absolute;
	width: 100%;
	left: 0;
	transition: all 0.3s ease;
	border-radius: 10px;
}

.menu_icon span:first-child {
	top: 0;
}

.menu_icon span:nth-child(2) {
	top: 6px;
}

.menu_icon span:last-child {
	top: 12px;
}

.menu_icon.active span:nth-child(2) {
	opacity: 0;
}

.menu_icon.active span:first-child,
.menu_icon.active span:last-child {
	top: 6px;
	background-color: $color_title;
}

.menu_icon.active span:first-child {
	transform: rotate(45deg);
}

.menu_icon.active span:last-child {
	transform: rotate(-45deg);
}
