@use '../helpers/variables' as *;
@use '../helpers/extends/extends' as *;
@use '../helpers/extends/buttons_extends' as *;
@use '../helpers/abstracts' as *;

.bestseller {
	display: flex;
	flex-direction: column;
}

.bestseller__title {
	position: relative;
	padding-left: 17.3rem;
	margin-bottom: 6.9rem;
}

.bestseller__title::before {
	position: absolute;
	content: '';
	top: 3rem;
	left: 0rem;
	width: 12rem;
	height: 0.4rem;
	background-color: $color_accent;
	margin-right: 4.3rem;
}

.bestseller__goods {
	position: relative;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	column-gap: 2rem;
	z-index: 1;

	@include bp($point_4) {
		display: flex;
		flex-direction: column;
	}


}


.bestseller__goods_no_decor {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	column-gap: 2rem;
}

.bestseller__goods::before {
	content: url(.././imgs/bestseller/dots.svg);
	position: absolute;
	top: 61rem;
	left: -3.5rem;

	@include bp($point_4) {
		top: 255rem;
		left: 15.5rem;
	}

	@include bp($point_6) {
		left: -2.5rem;
	}

	@include bp($point_7) {
		display: none;
	}
}

.bestseller__goods::after {
	content: url(.././imgs/bestseller/dots.svg);
	position: absolute;
	top: -3.5rem;
	right: -3.5rem;

	@include bp($point_4) {
		top: -3.5rem;
		right: 15.5rem;
	}

	@include bp($point_6) {
		right: -2.5rem;
	}

	@include bp($point_7) {
		display: none;
	}

}

.plitka__title {
	font-family: $font_2;
	font-size: 2.6rem;
	font-weight: 700;
	line-height: 1;
	text-align: center;
}

.plitka__block {
	width: calc((100% / 4) - 2rem);
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	row-gap: 1.8rem;
	background-color: #ffffff;
	border: 1px solid #f5f5f5;
	box-shadow: 0px 4px 20px 0px #0000001a;
	padding: 3.4rem;
	z-index: 1;

	@include bp($point_4) {
		min-width: 33.5rem;
	}

}

.plitka__img_block {
	display: block;
	width: 26.3rem;
	height: 25.3rem;
}

.plitka__img {
	width: 100%;
	height: 100%;
	object-fit: contain;
	object-position: center;
}

.plitka__info {
	font-family: $font_1;
	font-size: 1.6rem;
	font-weight: 400;
	line-height: 2.4rem;
	color: #555555;
}

.bestseller_span {
	font-family: $font_1;
	font-size: 2.5rem;
	font-weight: 500;
	line-height: 2.6rem;
	color: #111111;
}

.plitka__colors {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	align-items: center;
	row-gap: 2.2rem;
}

.plitka__color_circle {
	width: 2.5rem;
	height: 2.5rem;
	border-radius: 50%;
}

.plitka__color_circle.plitka__color_circle--red {
	background-color: #ff4141;
	margin-right: 2.2rem;
}

.plitka__color_circle.plitka__color_circle--brown {
	background-color: #8e5500;
	margin-right: 2.2rem;
}

.plitka__color_circle.plitka__color_circle--green {
	background-color: #4e760c;
	margin-right: 2.2rem;
}

.plitka__color_circle.plitka__color_circle--yellow {
	background-color: #f0c78a;
	margin-right: 2.2rem;
}

.plitka__color_circle.plitka__color_circle--black {
	background-color: #111111;
}

.plitka__color_circle.plitka__color_circle--grey {
	background-color: #a4a4a4;
}