.preparation__title {
	position: relative;
	margin-bottom: 6.8rem;
	@include bp($point_2, min) {
		grid-column: 1/3;
		grid-row: 1/2;
	}
	@include bp($point_3) {
		text-align: center;
	}
	&::after {
		content: '';
		position: absolute;
		bottom: -3.2rem;
		height: 0.4rem;
		background-color: $color_accent;
		@include bp($point_2, min) {
			left: 0;
		}
		@include bp($point_3) {
			left: calc(50% - 6rem);
		}
		@include bp($point_4, min) {
			width: 12rem;
		}
		@include bp($point_5) {
			width: 8.6rem;
		}
	}
}
.preparation__text {
	@include bp($point_2, min) {
		grid-column: 1/2;
		grid-row: 2/3;
	}
	@include bp($point_3){
		max-width: 72.8rem;
	}
}
.grid_box__pic_right.grid_box__pic_right--lower {
	@include bp($point_2, min) {
		grid-column: 2/3;
		grid-row: 2/3;
	}
}
.preparation {
	column-gap: 8.8rem;
}
.bace_text {
	font-weight: 400;
	color: $color_descr;
	@include bp($point_4, min) {
		font-size: 2rem;
		line-height: 1.5;
	}
	@include bp($point_5) {
		font-size: 1.6rem;
		line-height: 1.9;
	}
}
.preparation__pic {
	display: block;
	align-self: center;
	@include bp($point_4, min) {
		width: 73rem;
		height: 59.4rem;
	}
	@include bp($point_5) {
		max-width: 37rem;
		height: 30rem;
	}
	.preparation__img {
		@extend %cover_img;
	}
}
