@use '../helpers/variables' as *;
@use '../helpers/extends/extends' as *;

.delivery__info {
	display: flex;
	column-gap: 8.6rem;

	@media (max-width: $point_3) {
		flex-direction: column-reverse;
	}
}

.container__in.container__in--delivery {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	background-image: url(.././imgs/delivery/delivery_title_bg.jpg);
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;

	@media (min-width: $point_2) {
		height: 44rem;
	}

	@media (min-width: $point_4) and (max-width: $point_3) {
		height: 28rem;
	}

	@media (max-width: $point_5) {
		height: 20rem;
	}
}

.delivery__info_title {
	@media (min-width: $point_2) {
		position: relative;
		padding-left: 17.3rem;
		color: $color_title;
		margin-bottom: 3.2rem;
	}

	@media (max-width: $point_3) {
		display: none;
	}
}

.delivery__info_title::before {
	content: '';
	position: absolute;
	top: 3rem;
	left: 0rem;
	width: 12rem;
	height: 0.4rem;
	background-color: $color_accent;
	margin-right: 4.3rem;
}

.delivery__info_title_m {
	@media (max-width: $point_3) {
		position: relative;
		color: $color_title;
		margin-bottom: 11.2rem;
		display: flex;
		justify-content: center;
		align-items: center;
		text-align: center;
	}

	@media (min-width: $point_2) {
		display: none;
	}

	@media (max-width: $point_5) {
		margin-bottom: 5.8rem;
	}
}

.delivery__info_title_m::before {
	content: '';
	position: absolute;
	bottom: -3.2rem;
	left: calc(50% - 6rem);
	width: 12rem;
	height: 0.4rem;
	background-color: $color_accent;
}

.delivery__info_content {
	display: flex;
	justify-content: center;
	@media (max-width: $point_3) {
		margin-bottom: 10rem;
	}

	@media (max-width: $point_3) {
		margin-bottom: 5.8rem;
	}
}

.main_descr_1.main_descr_1--delivery__info {
	@media (min-width: $point_2) {
		padding-left: 17rem;
	}

	@media (max-width: $point_3) {
		padding: 0rem 10rem;
	}

	@media (max-width: $point_5) {
		font-size: 1.6rem;
		line-height: 1.87;
		padding: 0rem 2rem;
	}
}

.delivery__info_image {
	background-image: url(../imgs/delivery/delivery_info_main.jpg);
	background-position: center right;
	background-repeat: no-repeat;
	background-size: cover;

	display: block;
	position: relative;

	@media (min-width: $point_4) {
		height: 46rem;
		width: 46rem;
	}

	@media (max-width: $point_5) {
		height: 27rem;
		width: 27rem;
	}

	.content__img {
		@extend %cover_img;
	}
	&::before {
		content: '';
		position: absolute;
		background-color: $color_accent;
		z-index: -1;

		@media (min-width: $point_4) {
			top: -4.6em;
			right: -4.6em;
			width: 9.2em;
			height: 9.2em;
		}

		@media (max-width: $point_5) {
			top: -2.6em;
			right: -2.6em;
			width: 5.4em;
			height: 5.4em;
		}
	}
	&::after {
		content: '';
		position: absolute;
		z-index: -1;
		background-image: url(.././imgs/why_we/pic_decor.svg);
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;

		@media (min-width: $point_4) {
			top: 14.3em;
			left: -6.4em;
			width: 29.2em;
			height: 38.4em;
		}

		@media (max-width: $point_5) {
			top: 9.3em;
			left: -3.4em;
			width: 17.2em;
			height: 22.6em;
		}
	}
}
