@use '../helpers/abstracts' as *;

.container__bg {
	background-image: url(.././imgs/our_achievements/background_our_achievements.png);
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
}
.our_achievements {
	max-width: 124rem;
	min-height: 59.4rem;
	display: flex;
	align-items: center;
	margin: 0 auto;
	@include bp($point_4, min) {
		justify-content: space-between;
	}
	@include bp($point_5) {
		flex-direction: column;
		justify-content: center;
		row-gap: 11.6rem;
		padding-top: 4.5rem;
		padding-bottom: 8.4rem;
	}
}
.our_achievements__col {
	width: 19.6rem;
	display: flex;
	flex-direction: column;
	align-items: center;
}
.our_achievements__pic {
	display: block;
	width: 15.4rem;
	height: 15.4rem;
	margin-bottom: 4.6rem;
	.our_achievements__img {
		@extend %contain_img;
	}
}
.our_achievements__number {
	font-size: 8rem;
	font-weight: 700;
	line-height: 0.8;
	color: $color_white;
	margin-bottom: 1.6rem;
}
.our_achievements__desckr {
	font-size: 2.5rem;
	font-weight: 700;
	line-height: 1.16;
	text-align: center;
	color: $color_white;
}
