@use '../helpers/abstracts' as *;

.assortment_detail {
	display: flex;
	column-gap: 3.2rem;
	width: 100%;
	min-height: 54rem;
}

.assortment_detail__nav {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	row-gap: 1.6rem;

	width: 8.6%;
}

.assortment_detail__nav_link {
	width: 100%;
	height: 11rem;

	cursor: pointer;

	&.active {
		transform: scale(1.1);
		transition: all $transition_dur ease-in-out;
	}

	& .assortment_detail__nav_img {
		@extend %cover_img;
	}

	&:hover {
		outline: 0.3rem solid $color_accent;
	}
}

.assortment_detail__content {
	display: flex;
	width: 100%;
}

.assortment_detail__content_img_w {
	width: 41.8%;
	height: 100%;

	& .assortment_detail__content_img {
		@extend %cover_img;
	}
}

.assortment_detail__content_item {
	display: none;

	column-gap: 6.6rem;
	width: 100%;

	&.active {
		display: flex;
	}
}

.assortment_detail__content_title {
	position: relative;

	font-family: $font_1;
	font-size: 3rem;
	line-height: 1;
	color: $color_descr_2;
	padding-bottom: 7.4rem;

	& span {
		font-family: $font_2;
		font-size: 5rem;
		font-weight: 700;
		line-height: 1.14;
		color: $color_title;
	}

	&::after {
		position: absolute;
		bottom: calc((7.4rem - 0.4rem) / 2);
		left: 0;

		content: '';

		width: 12rem;
		height: 0.4rem;

		background-color: $color_accent;
	}
}

.assortment_detail__content_sizes {
	display: flex;
	align-items: center;
	column-gap: 1rem;

	margin-bottom: 2rem;
}

.assortment_detail__content_sizes__title,
.assortment_detail__content_colors__title {
	font-family: $font_1;
	font-size: 2rem;
	font-weight: 500;
	line-height: 1.5;
	color: $color_text_2;
}

.assortment_detail__content_colors {
	display: flex;
	align-items: center;
	column-gap: 1.4rem;

	margin-bottom: 4rem;
}

.assortment_detail__content_colors__title {
	padding-right: 2.4rem;
}

.assortment_detail__content_sizes__item {
	font-family: $font_1;
	font-size: 2rem;
	line-height: 1.5;
	color: $color_descr_1;

	text-decoration: none;

	transition: all $transition_dur ease-in-out;

	cursor: pointer;

	&:hover {
		color: $color_accent;
		text-decoration: underline;
	}
}

.assortment_detail__content_colors__item {
	width: 2.4rem;
	height: 2.4rem;

	transition: all $transition_dur ease-in-out;

	cursor: pointer;

	&:hover {
		transform: scale(1.15);
	}

	&.assortment_detail__content_colors__item--light_gray {
		background-color: #a6a8a2;
	}

	&.assortment_detail__content_colors__item--brick_red {
		background-color: #ba5a4d;
	}

	&.assortment_detail__content_colors__item--olive_green {
		background-color: #9a8f49;
	}

	&.assortment_detail__content_colors__item--beige {
		background-color: #c4987b;
	}

	&.assortment_detail__content_colors__item--dark_brown {
		background-color: #5b464b;
	}

	&.assortment_detail__content_colors__item--charcoal_black {
		background-color: #262721;
	}

	&.assortment_detail__content_colors__item--sky_blue {
		background-color: #4f95b0;
	}

	&.assortment_detail__content_colors__item--mint_green {
		background-color: #82ca87;
	}

	&.assortment_detail__content_colors__item--golden_yellow {
		background-color: #dfb44d;
	}

	&.assortment_detail__content_colors__item--orange {
		background-color: #d97131;
	}

	&.assortment_detail__content_colors__item--coral_red {
		background-color: #dc5e4c;
	}

	&.assortment_detail__content_colors__item--white {
		background-color: #fdfdfd;
		border: 1px solid #e5e4e4;
	}

	&.assortment_detail__content_colors__item--rainbow_gradient {
		background: linear-gradient(180deg, #ff8b8b 0%, #fff386 25.52%, #5fb558 51.56%, #41c2ca 73.44%, #4862ed 100%);
	}
}

.assortment_detail__content_info {
	display: flex;
	flex-direction: column;
	width: calc(100% - (6.6rem + 41.8%));
}

.assortment_detail__content_descr {
	max-width: 62rem;

	font-family: $font_1;
	font-size: 1.8rem;
	line-height: 1.66;
	color: $color_descr_1;

	flex-grow: 1;

	&:not(:last-child) {
		margin-bottom: 4rem;
	}
}

.assortment_detail__content_btns {
	display: flex;
	column-gap: 3.6rem;
}
