@use '../helpers/abstracts' as *;
@use '../helpers/mixins/breakpoints' as *;

.production_content {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	gap: 18rem;

	@include bp($point_2) {
		gap: 10rem;
	}


	@include bp($point_4) {
		display: flex;
		flex-direction: column-reverse;
		gap: 6.2rem;
	}

}

.production_content_col_2 {
	position: relative;

	@include bp($point_4) {
		display: flex;
		align-items: center;
		justify-content: center;
	}

}

.section_title__production {
	position: relative;
	margin-bottom: 2.6rem;
	padding-left: 17.3rem;

	@include bp($point_6) {
		font-size: 2.8rem;
		line-height: 6.3rem;
		padding-left: 13.1rem;
		text-wrap: nowrap;
	}

	@include bp($point_7) {
		text-wrap: nowrap;
		font-size: 2rem;
		line-height: 6.3rem;
	}


	&:before {
		position: absolute;
		content: '';
		top: 3rem;
		left: 0rem;
		width: 12rem;
		height: 0.4rem;
		background-color: $color_accent;
		margin-right: 4.3rem;

		@include bp($point_6) {
			width: 8.6rem;
			height: 4px;
		}

	}


}

.production_descr {
	font-family: $font_1;
	font-size: 2rem;
	font-weight: 400;
	line-height: 3rem;
	padding-left: 17.3rem;
	color: $color_descr_1;

	@include bp($point_2) {
		font-size: 1.5rem;
		padding-left: 0;

	}

	@include bp($point_6) {
		padding-left: 0;
	}

}

.picture_w {
	width: 46rem;
	height: 46rem;

	@include bp($point_2) {
		max-width: 40.2rem;
		height: 27rem;
	}

	@include bp($point_6) {
		max-width: 27rem;
		height: 27rem;
	}


	&:before {
		content: '';
		position: absolute;
		top: 1%;
		left: 100%;
		transform: translate(-50%, -50%);
		width: 9rem;
		height: 9rem;
		background-color: $color_accent;

		@include bp($point_4) {
			width: 5rem;
			height: 5rem;
		}

		@include bp($point_6) {
			width: 55px;
			height: 54px;
			left: 78%;
		}

		@include bp($point_7) {
			display: none;
		}
	}

}



.production_content_col_2::before {
	content: '';
	position: absolute;
	top: 24%;
	left: -13%;
	width: 29rem;
	height: 38rem;
	background: url(../imgs/production/decor_production.svg) no-repeat center/contain;

	@include bp($point_2) {
		width: 17.2rem;
		height: 22.5rem;
		top: 8%;
	}

	@include bp($point_4) {
		width: 17.2rem;
		height: 22.5rem;
		left: -4rem;
		top: 7rem;
	}

	@include bp($point_6) {
		width: 17.2rem;
		height: 22.5rem;
		top: 26%;
		left: 0%;
	}

	@include bp($point_7) {
		display: none;
	}
}

.production__img {
	position: relative;
	width: 100%;
	height: 100%;
	object-fit: cover;
}