@use '../helpers/abstracts' as *;

.container__in.container__in--price{
    position: relative;
    height: 44rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url(.././imgs/price_list_title/price_list_title__bg.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.price_list_title {
	display: flex;
}

.price_list_title__name {
	font-family: $font_2;
	font-size: 6rem;
	font-weight: 700;
	line-height: 1;
	text-align: center;
	color: $color_white;
	text-transform: uppercase;
}
