@use './helpers/abstracts' as *;
@use './helpers/mixins/breakpoints' as *;

.footer {
	padding: 4.4rem 0;
}


.footer__content {
	display: flex;
	column-gap: 14.6rem;
	min-height: 12.9rem;
	align-items: center;

	@include bp($point_2) {
		display: flex;
		flex-direction: column;
		gap: 5.6rem;

	}

}

.footer__item {
	display: flex;
	flex-direction: column;

	@include bp($point_4) {
		align-items: center;
	}

}

.footer__item_title {
	font-family: $font_2;
	font-size: 3rem;
	font-weight: 700;
	line-height: 1.28;
	color: $color_title;

	position: relative;

	padding-bottom: 3rem;

	&::after {
		position: absolute;
		bottom: calc((3rem - 0.4rem) / 2);
		left: 0;
		/* 		transform: translate(-50%, -50%);
 */
		content: '';
		width: 12rem;
		height: 0.4rem;
		background-color: $color_accent;

		@include bp($point_4) {
			left: 50%;
			transform: translate(-50%, -50%);

		}

	}
}

.footer__item_text {
	&.footer__item_text--phone_numbers {
		display: flex;
		flex-direction: column;

		& .footer__phone_number {
			text-decoration: none;
			color: $color_title;

			transition: all $transition_dur ease-in-out;

			&:hover {
				color: $color_accent;
				text-decoration: underline;
			}
		}
	}

	font-family: $font_1;
	font-size: 2rem;
	line-height: 1.5;
	color: $color_title;
}