@use '../helpers/abstracts' as *;
.dry_pressed_tiles__accord {
	display: flex;
	flex-direction: column;
	row-gap: 0.2rem;

	&:not(:last-child) {
		padding-bottom: 9rem;
	}
}

.dry_pressed_tiles__title {
	position: relative;

	display: flex;
	align-items: center;
	justify-content: space-between;
	column-gap: 2rem;

	min-height: 18rem;

	padding: 0 5rem;

	background-color: $color_section_bg_2;

	cursor: pointer;

	&.active {
		& .dry_pressed_tiles__arrow::before {
			transform: translateY(-50%) rotate(225deg);
		}
		& .dry_pressed_tiles__arrow::after {
			transform: translateY(-50%) rotate(135deg);
		}

		& + .dry_pressed_tiles__content {
			opacity: 1;
			visibility: visible;
			pointer-events: auto;
			height: auto;

			transition: all $transition_dur ease-in-out;
		}
	}

	&::after {
		position: absolute;
		right: 12rem;

		content: '';

		width: 1px;
		height: 100%;

		background-color: #dcdcdc;
	}
}

.dry_pressed_tiles__title__col {
	display: flex;
	align-items: center;
	column-gap: 10rem;
}

.dry_pressed_tiles__title__text {
	display: flex;
	flex-direction: column;

	font-family: $font_2;
	font-size: 2.6rem;
	font-weight: 700;
	line-height: 1.38;
	color: $color_title;

	text-wrap: nowrap;

	& span {
		font-family: $font_1;
		font-size: 2rem;
		line-height: 1.5;
		color: $color_descr_1;
	}
}

.dry_pressed_tiles__title__img_w {
	display: block;

	&.dry_pressed_tiles__title__img_w--old_town {
		width: 15rem;
		height: 15rem;
	}

	&.dry_pressed_tiles__title__img_w--kirpichik {
		width: 16.4rem;
		height: 9.5rem;
	}

	&.dry_pressed_tiles__title__img_w--lviv_stone {
		width: 17.6rem;
		height: 17.6rem;
	}

	&.dry_pressed_tiles__title__img_w--creative {
		width: 17.2rem;
		height: 13rem;
	}
}

.dry_pressed_tiles__title__img {
	@extend %cover_img;
}

.dry_pressed_tiles__arrow {
	position: relative;

	display: block;
	width: 2.2rem;
	height: 1.1rem;

	&::after,
	&::before {
		content: '';
		position: absolute;
		width: 1.4rem;
		height: 0.3rem;
		background-color: #111;
		transition: transform 0.3s ease;
	}

	&::before {
		top: 0;
		left: 0;
		transform: translateY(-50%) rotate(135deg);
	}

	&::after {
		top: 0;
		left: 40%;
		transform: translateY(-50%) rotate(225deg);
	}
}

.dry_pressed_tiles__content {
	display: flex;
	flex-direction: column;
	row-gap: 0.2rem;

	opacity: 0;
	visibility: hidden;
	pointer-events: none;
	height: 0;
}

.dry_pressed_tiles__value {
	display: flex;
	align-items: center;
	justify-content: space-between;
	column-gap: 2rem;

	background-color: #caf3d3;

	padding: 0 5rem;

	min-height: 8.6rem;
}

.dry_pressed_tiles__colors,
.dry_pressed_tiles__prices_col1,
.dry_pressed_tiles__prices_col2 {
	font-family: $font_1;
	font-size: 2rem;
	font-weight: 500;
	line-height: 1.5;

	color: $color_text_2;
}

.dry_pressed_tiles__prices {
	display: flex;
	column-gap: 20.6rem;
}

.dry_pressed_tiles__item {
	display: flex;
	align-items: center;
	justify-content: space-between;
	min-height: 8.8rem;

	&:nth-child(2n) {
		background-color: $color_section_bg_2;
	}

	&:nth-child(2n-1) {
		background-color: $color_section_bg_3;
	}

	padding: 0 8rem 0 3.6rem;
}

.dry_pressed_tiles__color {
	display: flex;
	align-items: center;
	column-gap: 4.6rem;
}

.dry_pressed_tiles__color_img_w {
	display: block;

	&.dry_pressed_tiles__color_img_w--old_town {
		width: 19.6rem;
		height: 6.8rem;
	}

	&.dry_pressed_tiles__color_img_w--kirpichik {
		width: 14rem;
		height: 7rem;
	}

	&.dry_pressed_tiles__color_img_w--lviv_stone {
		width: 18.2rem;
		height: 8.6rem;
	}

	&.dry_pressed_tiles__color_img_w--creative {
		width: 8.8rem;
		height: 8.8rem;
	}
}

.dry_pressed_tiles__color_img {
	@extend %cover_img;
}

.dry_pressed_tiles__color_name {
	font-family: $font_1;
	font-size: 2rem;
	font-weight: 400;
	line-height: 1.5;

	color: $color_text_2;
}

.dry_pressed_tiles__price {
	display: flex;
	align-items: center;
	column-gap: 26.8rem;
}

.dry_pressed_tiles__price_col1,
.dry_pressed_tiles__price_col2 {
	font-family: $font_1;
	font-size: 2.4rem;
	font-weight: 700;
	line-height: 1.25;

	color: $color_text_2;
}

.dry_pressed_tiles__price_col1 {
	&:not(:has(+ .dry_pressed_tiles__price_col2)) {
		margin-right: 35.3rem;
	}
}
