@use '../helpers/variables' as *;
@use '../helpers/extends/extends' as *;
@use '../helpers/extends/buttons_extends' as *;

.news {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.news__title {
	position: relative;
	padding-left: 17.3rem;
	margin-bottom: 10.1rem;
}

.news__title::before {
	content: '';
	position: absolute;
	top: 3rem;
	left: 0rem;
	width: 12rem;
	height: 0.4rem;
	background-color: $color_accent;
	margin-right: 4.3rem;
}
.news__container {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.news__block {
	width: calc(100% / 2 - 3rem);
}
.news__block.news__block_picture {
	position: relative;
}

.news__picture_block {
	background-image: url(./imgs/news/plitka_news.png);
	background-position: center;
	background-repeat: no-repeat;
}

.white_infoblock {
	position: absolute;
	top: 47rem;
	right: 0rem;
	width: 58%;
	min-height: 15.8rem;
	height: 25%;
	background-color: white;
}
.news_picture {
	width: 100%;
	height: 100%;
	object-position: center;
	object-fit: contain;
}

.news__picture_info {
	font-family: $font_2;
	font-size: 2.6rem;
	font-weight: 700;
	line-height: 1.2;
	color: $color_title;
	padding: 3rem;
}

.news__block.news__block--news {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	row-gap: 3rem;
}

.single_news {
	display: flex;
	flex-direction: row;
	row-gap: 3rem;
	align-items: start;
}
.news__year {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.news__year_single {
	font-family: $font_1;
	font-size: 2rem;
	font-weight: 400;
	line-height: 1.5;
	padding-right: 3rem;
	padding-top: 3rem;
}

.news__descr_small {
	font-family: $font_1;
	font-size: 2rem;
	font-weight: 400;
	line-height: 1.5;
	color: $color_descr_1;
	text-align: start;
	padding-left: 3.9rem;
}

.single__news_descr {
	display: flex;
	flex-direction: column;
	border-left: 4px solid $color_accent;
	padding-left: 1.6rem;
}
.single__news_descr.single__news_descr--all {
	border-left: none;
}
.single__news_descr_date {
	font-family: $font_1;
	font-size: 1.6rem;
	font-weight: 500;
	line-height: 3rem;
	color: #999999;
}
.single__news_descr_title {
	font-family: $font_2;
	font-size: 2.6rem;
	font-weight: 700;
	line-height: 3.2rem;
	color: $color_title;
}
.read_all_news {
	display: flex;
	flex-direction: row;
	align-items: center;
}
.single__news_descr_title.single__news_descr_title--all {
	font-family: $font_1;
	font-size: 2rem;
	font-weight: 700;
	line-height: 1.5;
	letter-spacing: 0.075em;
	color: $color_accent;
	text-transform: lowercase;
	text-decoration: none;
}

.arrow_right_block {
	width: 3rem;
	height: 3rem;
	margin-left: 1.9rem;
}

.arrow_right {
	width: 100%;
	height: 100%;
	object-fit: contain;
	object-position: center;
}
