@use '../helpers/variables' as *;
@use '../helpers/extends/extends' as *;
@use '../helpers/extends/buttons_extends' as *;

.main_title.main_title--news1 {
	font-family: $font_2;
	font-size: 6rem;
	font-weight: 700;
	line-height: 1;
	text-align: center;
	color: $color_white;
	text-transform: uppercase;
}

.news__container.news__container--news2 {
	display: flex;
	flex-wrap: wrap;
	column-gap: 3rem;
	row-gap: 6.4rem;
	justify-content: center;
	align-items: center;
}
.news__title.news__title--news1 {
	margin-bottom: 6.4rem;
}
.container__in.container__in--news{
    position: relative;
    height: 44rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url(.././imgs/news/news_background.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}
