@use '../helpers/variables' as *;

.paving_tiles_content {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	gap: 3.8rem;
	margin-top: 6.6rem;
	margin-bottom: 11.4rem;
	flex-wrap: wrap;

	&__item {
		display: block;
		width: 25.3rem;
		height: 24.4rem;
		text-decoration: none;
		text-align: center;
		color: $color_black;
	}

	&__picture {
		width: 100%;
		height: 69%;
	}

	&__title {
		display: block;
		font-family: $font_1;
		font-size: 1.8rem;
		font-weight: 500;
		line-height: 3rem;
		text-align: center;
		margin-top: 1.8rem;
	}
}
