@use '../helpers/variables' as *;
@use '../helpers/extends/extends' as *;
@use '../helpers/extends/buttons_extends' as *;
@use '../helpers/mixins/breakpoints' as *;

.block_title {
	position: relative;
	margin-bottom: 13rem;

	& .title_causes {
		font-size: 5rem;
		font-weight: 700;
		line-height: 6.3rem;
		text-align: center;

		@include bp($point_6) {
			font-size: 2.8rem;
			line-height: 4rem;

		}

	}

	::after {
		position: absolute;
		content: ' ';
		height: 0.4rem;
		width: 12rem;
		position: absolute;
		background-repeat: no-repeat;
		background-color: $color_accent;
		bottom: -2rem;
		left: 50%;
		transform: translate(-50%, -50%);


		@include bp($point_6) {
			width: 8.6rem;
		}

	}
}

.block_causes {
	display: flex;
	column-gap: 2rem;

	@include bp($point_2) {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		gap: 1rem;
	}


	@include bp($point_4) {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 1rem;
	}


	& .block_causes__in {
		display: flex;
		align-items: center;
		flex-direction: column;
		row-gap: 3.6rem;
		width: 22rem;

		@include bp($point_4) {
			width: auto;
		}



		& .box_causes {
			display: flex;
			width: 13.8rem;
			height: 13.8rem;
			border-radius: 50%;
			background-color: $color_accent;

			@include bp($point_6) {
				width: 10rem;
				height: 10rem;
			}
		}

		& .box_descr {
			width: 100%;

			& .descr_causes {
				color: $color_descr_1;
				font-size: 2rem;
				font-weight: 700;
				line-height: 3rem;
				text-align: center;

				@include bp($point_6) {
					font-size: 1.4rem;
					line-height: 2rem;
				}

			}
		}

		& .img_causes {
			display: block;
			margin: auto;
		}
	}
}