@use '../helpers/variables' as *;
@use '../helpers/extends/extends' as *;

.delivery__form_sub_title {
	font-family: $font_1;
	font-weight: 400;
	line-height: 1.38;
	color: $color_title;

	@media (min-width: $point_2) {
		font-size: 2.4rem;
		margin-bottom: 2.8rem;
	}

	@media (min-width: $point_4) and (max-width: $point_3) {
		font-size: 1.8rem;
		margin-bottom: 2.4rem;
	}
}

.delivery__form_text {
	font-family: $font_1;
	font-size: 2.8rem;
	line-height: 1;
	color: $color_descr_1;
	margin-bottom: 2.8rem;

	@media (min-width: $point_2) {
		font-size: 2.8rem;
		margin-bottom: 2.8rem;
	}

	@media (min-width: $point_4) and (max-width: $point_3) {
		font-size: 2.2rem;
		margin-bottom: 2.2rem;
	}

	& span {
		color: $color_accent;
	}
}

.delivery__form_name {
	position: relative;

	&::before {
		position: absolute;
		content: '';
		background-image: url(../imgs/delivery/delivery_form_1.svg);
		background-position: center;
		background-repeat: no-repeat;
		background-size: contain;
		z-index: 1;
	}

	@media (min-width: $point_2) {
		margin-bottom: 3.4rem;

		&::before {
			top: 3.3rem;
			left: 2.4rem;
			width: 2.5rem;
			height: 2.5rem;
		}
	}

	@media (min-width: $point_4) and (max-width: $point_3) {
		margin-bottom: 2.4rem;

		&::before {
			top: 2.5rem;
			left: 1.6rem;
			width: 2.2rem;
			height: 2.2rem;
		}
	}
}

.delivery__form_num {
	position: relative;

	&::before {
		position: absolute;
		content: '';
		background-image: url(../imgs/delivery/delivery_form_2.svg);
		background-position: center;
		background-repeat: no-repeat;
		background-size: contain;
		z-index: 1;
	}

	@media (min-width: $point_2) {
		margin-bottom: 3.4rem;

		&::before {
			top: 3.3rem;
			left: 2.4rem;
			width: 2.8rem;
			height: 2.8rem;
		}
	}

	@media (min-width: $point_4) and (max-width: $point_3) {
		margin-bottom: 2.4rem;

		&::before {
			top: 2.5rem;
			left: 1.6rem;
			width: 2.2rem;
			height: 2.2rem;
		}
	}
}

.delivery__form_input {
	font-family: $font_1;
	line-height: 1;
	color: $color_black;
	border: 1px solid #9e9e9e;

	@media (min-width: $point_2) {
		width: 60rem;
		font-size: 2.8rem;
		padding: 2.8rem 0 2.8rem 7rem;
	}

	@media (min-width: $point_4) and (max-width: $point_3) {
		width: 48rem;
		font-size: 2.2rem;
		padding: 2.2rem 0 2.2rem 5rem;
	}
}

.delivery__form_input::placeholder {
	font-family: $font_1;
	font-size: 2.8rem;
	font-style: italic;
	line-height: 1;
	color: #e3e3e3;
}
