@use '../helpers/abstracts' as *;

.grid_box {
	@include bp($point_2, min) {
		display: grid;
		grid-template-columns: repeat(2, auto);
		grid-template-rows: repeat(3, auto);
	}
	@include bp($point_3) {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
}
.why_we {
	max-width: 126.8rem;
	column-gap: 9.4rem;
	padding-top: 6.1rem;
	padding-right: 6.4rem;
}
.grid_box__title {
	@include bp($point_3) {
		order: -1;
	}
}
.grid_box__pic_left {
	@include bp($point_2, min) {
		grid-row: 1 / span 3;
	}
	@include bp($point_3) {
		order: 2;
	}
}
.grid_box__text {
	@include bp($point_3) {
		order: 3;
		max-width: 72.8rem;
	}
}
.grid_box__btn {
	display: block;
	@include bp($point_3) {
		order: 4;
	}
}
.why_we__pic {
	display: block;
	width: 51.2rem;
	height: 34.4rem;
	position: relative;
	z-index: 1;
	.content__img {
		@extend %cover_img;
	}
	&::before {
		content: '';
		position: absolute;
		top: -6.1em;
		left: 4.4em;
		width: 38.6em;
		height: 6.1em;
		background-color: $color_accent;
	}
	&::after {
		content: '';
		position: absolute;
		top: 6.3em;
		left: -6.4em;
		width: 29.2em;
		height: 38.4em;
		z-index: -1;
		background-image: url(.././imgs/why_we/pic_decor.svg);
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
	}
}
.why_we__title {
	margin-bottom: 3.6rem;
}

.why_we__text {
	max-width: 59.8rem;
	margin-bottom: 3.6rem;
}

.title_decor {
	display: inline-block;
	position: relative;
	width: 19.8rem;
	&::before {
		content: '';
		position: absolute;
		top: -1.3rem;
		left: 0;
		width: 12rem;
		height: 0.4rem;
		background-color: $color_accent;
	}
}
.bace_text > span {
	display: block;
	font-size: 2rem;
	font-weight: 400;
	line-height: 1.5;
	color: $color_descr;
}
.bace_text > span:first-child {
	font-weight: 500;
}
