@use '../helpers/variables' as *;
@use '../helpers/extends/extends' as *;
@use '../helpers/extends/buttons_extends' as *;

.goods_description__block{
    display: flex;
    flex-direction: column;
}
.goods_description__buttons{
    display: flex;
    justify-content: center;
    column-gap: 8.1rem;
    text-decoration: none;
    margin-bottom: 7.1rem;
}
.goods_description__name{
    font-family: $font_2;
font-size: 3rem;
font-weight: 700;
line-height: 2.1;
color: $color_title;
text-decoration: none;

}
.btn_grey{
width: 21.3rem;
height: 6.1rem;
border-radius: 5.2rem;
background-color: #9D9D9D;
text-decoration: none;
display: flex;
justify-content: center;
align-items: center;
font-family: $font_2;
font-size: 3rem;
font-weight: 700;
line-height: 2.1;
color: $color-white

}
.goods_description__text{
    font-family: $font_1;
font-size: 1.8rem;
font-weight: 400;
line-height: 30px;
padding-top: 7.1rem;
color: $color_descr_2;

}
.non_bold{
    font-family: $font_1;
    font-size: 1.8rem;
    font-weight: 400;
    line-height: 1.66;
    color: $color_descr_1;
}
.goods_description__block.goods_description__block--narrow{
    display: flex;
    max-width: 59rem;
    justify-content: center;

}
.assortment_detail__content_list{
    display: flex;
    flex-direction: column;
    font-family: $font_1;
    font-size: 1.8rem;
    font-weight: 700;
    line-height: 1.66;   
}

.assortment_detail_new_line{
    margin-bottom: 2.7rem;
}