@use '../helpers/variables' as *;
@use '../helpers/extends/extends' as *;
@use '../helpers/extends/buttons_extends' as *;

.callback_container {
	display: flex;
}
.callback_column {
	width: 50%;
}

.callback__map {
	width: 100%;
	height: 100%;
	object-fit: contain;
	object-position: center;
}
.callback_form_wrapper {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: start;
	padding-left: 14.6rem;
}
.callback__form_title {
	position: relative;
	padding-left: 16.3rem;
	margin-bottom: 4.9rem;
	padding-top: 11.7rem;
}
.callback__form_title::before {
	position: absolute;
	content: '';
	top: 14.7rem;
	left: 0rem;
	width: 12rem;
	height: 0.4rem;
	background-color: $color_accent;
	margin-right: 4.3rem;
}
.callback__form_descr {
	font-family: $font_1;
	font-size: 2.4rem;
	font-weight: 400;
	line-height: 1.37;
	margin-bottom: 5.6rem;
}
.input_info {
	font-family: $font_1;
	font-size: 2.8rem;
	font-weight: 400;
	line-height: 0.92;
	margin-bottom: 2.6rem;
}

.input_info_field_name,
.input_info_field_phone {
	width: 100%;
	height: 7.6rem;
	border: 1px;
	margin-bottom: 3.3rem;
	font-family: Roboto;
	font-size: 28px;
	font-weight: 400;
	line-height: 0.92;
	border-right: 1px solid #9e9e9e;
	border-top: 1px solid #9e9e9e;
	border-bottom: 1px solid #9e9e9e;
}
.input_info_field_phone {
	margin-bottom: 8.3rem;
}
.input_name_wrapper {
	width: 60%;
	display: flex;
	justify-content: start;
	position: relative;
}

.input_phone_wrapper {
	width: 60%;
	display: flex;
	justify-content: start;
}

.phone_icon_wrapper {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 7.6rem;
	height: 7.6rem;
	background-color: white;
	border-left: 1px solid #9e9e9e;
	border-top: 1px solid #9e9e9e;
	border-bottom: 1px solid #9e9e9e;
}
.phone_icon_wrapper_small {
	width: 2.5rem;
	height: 2.5rem;
	background-color: white;
}

.phone_icon_img {
	width: 100%;
	height: 100%;
	object-fit: contain;
	object-position: center;
}

.green_dot {
	color: $color_accent;
}

.input_info_field_phone::placeholder,
.input_info_field_name::placeholder {
	font-family: $font_1;
	font-size: 2.8rem;
	font-weight: 400;
	line-height: 0.92;
	color: #e3e3e3;
}
