@use './helpers/abstracts' as *;

html {

	// font-size: calc(100dvw / 1920 * 10);
	// font-size: 10px;
	@include bp($point_2, min) {
		font-size: calc(100dvw / 1920 * 10);
	}

	@include bp($point_3) {
		font-size: 10px;
	}
}

html,
body {
	height: 100%;
	font-family: $font_1;
	font-weight: 400;
	scroll-behavior: smooth;
}

.wrapper {
	width: 100%;
	display: flex;
	flex-direction: column;
	min-height: 100%;
}

.main {
	flex-grow: 1;

	&.main--price_list {
		overflow: hidden;
	}
}

.main_logo_w {
	display: block;
	flex-shrink: 0;
	transition: height $transition_dur ease-in-out;

	@include bp($point_4, min) {
		width: 28.8rem;
		height: 6.8rem;
	}

	@include bp($point_5) {
		width: 20.3rem;
		height: 6.8rem;
	}


	.header.header--scroll & {
		height: 4rem;
	}
}

.main_logo {
	@extend %contain_img;
}

.section {
	padding: 6rem 0;
	width: 100%;

	&.section--bg {
		background-color: $color_section_bg;
	}

	&.section--center {
		display: flex;
		justify-content: center;
		align-items: center;
	}

	&.section--photo_gallery {
		background-color: $color_section_bg;
	}

	&.section--no_padding {
		padding: 0;
	}

	&.section--production {
		padding-top: 25rem;
	}

	&.section--assortment_detail {
		padding: 25rem;
	}

	&.section--delivery_title {
		display: flex;
		align-items: center;
		justify-content: center;

		background-image: url(../imgs/delivery/delivery_title_bg.jpg);
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		height: 44.4rem;
	}

	&.section--delivery_form {
		background-image: url(../imgs/delivery/delivery_form_main.jpg);
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;

		@media (min-width: $point_2) {
			height: 65rem;
		}


		@media (min-width: $point_4) and (max-width: $point_3) {
			height: 39rem;
		}

	}

	&.section--news1_title {
		display: flex;
		align-items: center;
		justify-content: center;

		background-image: url(../imgs/news/news_background.jpg);
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		height: 44.4rem;
		padding-top: 14.7rem;
	}

	&--main_page_banner {
		min-height: 83.8rem;
		background-color: $color_section_bg;
	}

	&.section--services_banner {
		padding-top: 14.7rem;
	}

	&.section--big_margen_bottom {
		margin-bottom: 12rem;
	}

	&--paving_tiles {
		background-color: $color_section_bg;
	}
}

.section_title {
	font-family: $font_2;
	font-weight: 700;
	color: $color_title;

	@include bp($point_4, min) {
		font-size: 5rem;
		line-height: 1.26;
	}

	@include bp($point_5) {
		font-size: 2.8rem;
		line-height: 1.4;
	}

	@media (min-width: $point_4) {
		font-size: 5rem;
	}

	&.section_title--dry_pressed_tiles {
		position: relative;

		padding-bottom: 11.2rem;

		text-align: center;

		&::after {
			position: absolute;
			bottom: 7.6rem;
			left: calc(50% - 6rem);
			content: '';
			width: 12rem;
			height: 0.4rem;
			background-color: $color_accent;
		}
	}

	&__main_page {
		position: relative;
		padding-top: 10.9rem;
		margin-bottom: 5.9rem;
		line-height: 6rem;
		color: $color_white;

		&::after {
			position: absolute;
			content: '';
			width: 12rem;
			height: 0.5rem;
			bottom: -2.8rem;
			left: 0;
			background-color: $color_accent;
		}
	}

	&--paving_tiles {
		position: relative;
		text-align: center;
		margin-bottom: 9.1rem;

		&::after {
			position: absolute;
			content: '';
			// width: 12rem;
			height: 0.4rem;
			bottom: -3.4rem;
			left: 50%;
			transform: translateX(-50%);
			background-color: $color_accent;

			@include bp($point_4, min) {
				width: 12rem;
			}

			@include bp($point_5) {
				width: 8.6rem;
			}
		}
	}

	@media (max-width: $point_5) {
		font-size: 2.8rem;
	}

	&--our_assortment {
		position: relative;
		padding-bottom: 3.6rem;
		padding-left: 17.3rem;

		@include bp($point_5) {
			padding-left: 12.3rem;
		}


		&::after {
			content: "";
			position: absolute;
			top: 3rem;
			left: 1rem;
			width: 12rem;
			height: 0.4rem;
			background-color: #28bd25;
			margin-right: 4.3rem;

			@include bp($point_4) {
				width: 8.6rem;
				height: 0.4rem;

			}
		}


	}
}


.container {
	padding: 0 2rem;
	max-width: 146rem;
	width: 100%;

	margin: 0 auto;

	@include bp($point_4, min) {
		max-width: 146rem;
	}

	@include bp($point_5) {
		max-width: 37.5rem;
	}

	&.container--center {
		display: flex;
		justify-content: center;
		align-items: center;
	}

	&.container--max_width {
		padding: 0;
		max-width: 192rem;
	}

	&.container--price_list_title {
		min-height: 44.4rem;
		background-image: url(../imgs/price_list_title/price_list_title__bg.jpg);
		background-position: center;
		background-repeat: no-repeat;
		background-size: contain;
	}

	&.container--main_page {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		z-index: 10;
	}

	&.container--column {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	&.container--lg {
		@include bp($point_4, min) {
			max-width: 152.6rem;
		}
	}

	&.container--in_left {
		@include bp($point_2, min) {
			display: flex;
			flex-direction: column;
			align-items: end;
		}
	}

	&.container--small {
		max-width: 107rem;
	}
}

.main__text {
	font-family: $font_2;
	font-size: 3rem;
	font-weight: 700;
	line-height: 1.66;
	text-align: center;
	margin-bottom: 0.9rem;
	color: $color_title;
}

.main_descr_1 {
	font-family: $font_1;
	// font-size: 2rem;
	font-weight: 400;
	// line-height: 1.5;
	color: $color_descr_1;

	@include bp($point_4, min) {
		font-size: 2rem;
		line-height: 1.5;
	}

	@include bp($point_5) {
		font-size: 1.6rem;
		line-height: 1.9;
	}

	&--paving_tiles {
		text-align: center;

		&:last-child {
			margin-bottom: 4rem;
		}
	}

	&--our_assortment {
		padding-left: 17.3rem;
		margin-bottom: 10.8rem;
		
		@include bp($point_4) {
			padding-left: 0;

		}
		
	}
}

.main_descr_2 {
	font-family: $font_1;
	font-size: 1.8rem;
	line-height: 1.44;
	text-align: center;
	color: $color_descr_2;
}

.main_title {
	font-family: $font_2;
	font-weight: 700;
	line-height: 1;
	text-align: center;
	color: $color_white;
	text-transform: uppercase;

	@media (min-width: $point_2) {
		font-size: 6rem;
	}

	@media (min-width: $point_4) and (max-width: $point_3) {
		font-size: 5rem;
	}

	@media (max-width: $point_5) {
		font-size: 3.5rem;
		line-height: 1.2;
	}
}