// }
.for_car {
	max-width: 124.6rem;
	column-gap: 12.8rem;
}
.for_car__title {
	position: relative;
	&::after {
		content: '';
		position: absolute;
		width: 12rem;
		height: 0.4rem;
		background-color: $color_accent;
		@include bp($point_2, min) {
			left: -17rem;
			top: 3rem;
		}
		@include bp($point_3) {
			left: calc(50% - 6rem);
			bottom: -2.3rem;
		}
	}
	@include bp($point_2, min) {
		max-width: 66.2rem;
		grid-column: 1/3;
		grid-row: 1/2;
		margin-bottom: 3.6rem;
	}
	@include bp($point_3) {
		margin-bottom: 9rem;
	}
	@include bp($point_5) {
		text-align: center;
	}
}
.for_car__text {
	margin-bottom: 4rem;
	@include bp($point_2, min) {
		max-width: 59.9rem;
		grid-column: 1/2;
		grid-row: 2/3;
	}
}
.grid_box__pic_right {
	@include bp($point_2, min) {
		grid-column: 2/3;
		grid-row: 1/3;
		order: 2;
	}
	@include bp($point_3) {
		margin-bottom: 8rem;
	}
}
.for_car__pic {
	position: relative;
	display: block;
	@include bp($point_4, min) {
		max-width: 46rem;
		height: 46rem;
	}
	@include bp($point_5) {
		max-width: 27rem;
		height: 27rem;
	}
	.for_car__img {
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: right bottom;
	}
	&::before {
		content: '';
		position: absolute;
		z-index: -1;
		background-image: url(.././imgs/why_we/pic_decor.svg);
		background-repeat: no-repeat;
		background-position: center;
		background-size: contain;
		@include bp($point_4, min) {
			width: 29.2rem;
			height: 38.3rem;
			left: -6.3rem;
			top: 13.4rem;
		}
		@include bp($point_5) {
			width: 17.1rem;
			height: 22.5rem;
			left: -3.7rem;
			bottom: -3.3rem;
		}
	}
	&::after {
		content: '';
		position: absolute;
		z-index: -1;
		background-color: $color_accent;
		@include bp($point_4, min) {
			right: -4.6rem;
			top: -4.6rem;
			width: 9.2rem;
			height: 9.2rem;
		}
		@include bp($point_5) {
			right: -2.7rem;
			top: -2.7rem;
			width: 5.4rem;
			height: 5.4rem;
		}
	}
}
.btn_white {
	@include bp($point_4, min) {
		min-width: 28.2rem;
	}
	@include bp($point_5) {
		min-width: 33.7rem;
	}
}
