@use '../helpers/variables' as *;

.main_page__subtitle {
    font-family: $font_1;
    font-size: 2.6rem;
    font-weight: 500;
    line-height: 4rem;
    color: $color_white;
    margin-bottom: 2.6rem;
}

.main-page-banner-swiper {
    overflow: hidden;
    position: relative;
    width: 100%;
}


.swiper-slide .main_page_banner__arrow {
    width: 100%;
    height: 100%;
    object-fit: cover;

}

.swiper-button-next,
.swiper-button-prev {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 10;
    cursor: pointer;
    width: 30px;
    height: auto;
}

.swiper-button-next {
    right: 0;
    width: 5.4rem;
    height: 7.1rem;
    background-color: rgba($color_white, 0.25);
    
}

.swiper-button-prev {
    left: 0;
    width: 5.4rem;
    height: 7.1rem;
    background-color: rgba($color_white, 0.25);
}

.swiper-button-next img,
.swiper-button-prev img {
    width: 2.6rem;
    height: 1.7rem;
}

.swiper-button-prev:after,
.swiper-rtl .swiper-button-next:after,
.swiper-button-next:after,
.swiper-rtl .swiper-button-prev:after {
    content: '';
}