@use '../helpers/variables' as *;
@use '../helpers/extends/extends' as *;
@use '../helpers/extends/buttons_extends' as *;


.section.section--news {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	min-height: 40.8rem;
	background-image: url(.././imgs/main_news/background_img.png);
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	z-index: -1;
}
.main_news__footer{
	display: flex;
	flex-direction: column;
	justify-content: start;
	align-items: start;
}
.news__container {
	display: flex;
	flex-direction: row;
}

.main_news__title {
	position: relative;
	padding-top: 10.6rem;
	margin-bottom: 2.7rem;
}

.main_news__title::before {
	content: '';
	position: absolute;
	top: 13rem;
	left: 0rem;
	width: 12rem;
	height: 0.4rem;
	background-color: $color_accent;
	margin-right: 4.3rem;
}

.personalisation_field {
	display: flex;
	justify-content: start;
	align-items: center;
	margin-bottom: 5.1rem;
}
.news_title_date {
	font-family: $font_1;
	font-size: 2rem;
	font-weight: 400;
	line-height: 1.5;
	color: #777777;
	margin-left: 1.4rem;
}

.news_title_person {
	font-family: $font_1;
	font-size: 2rem;
	font-weight: 400;
	line-height: 1.5;
	color: #777777;
	margin-left: 1.4rem;
}
.data__icon_block.data__icon_block--person {
	margin-left: 1.4rem;
}
.data__icon_block {
	width: 1.5rem;
	height: 1.5rem;
}
.data__icon_block.data__icon_block--date {
	margin-left: 16.9rem;
}
.data__icon_img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: contain;
	object-position: center;
}

.title__photo_block {
	width: 141.6rem;
	height: 46rem;
	margin-bottom: 7rem;
}
.title__photo_img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: contain;
	object-position: center;
}
.news__block_title {
	font-family: $font_2;
	font-size: 2.6rem;
	font-weight: 700;
	line-height: 1.23;
	padding-bottom: 3.6rem;
}

.news__block_text_span {
	font-family: $font_1;
	font-size: 3rem;
	font-weight: 400;
	line-height: 1.5;
	padding: 7rem 0;
}
.news__block_text {
	font-family: $font_1;
	font-size: 2rem;
	font-weight: 400;
	line-height: 1.5;
	padding-bottom: 7rem;
}
.news__block.news__block--main_news {
	display: flex;
	flex-direction: column;
	row-gap: 5rem;
}


.main_news__footer_text {
	max-width: 108.1rem;
	font-family: $font_2;
	font-size: 2.6rem;
	font-weight: 700;
	line-height: 1.23;
	color: $color-white;
	padding-top: 8.4rem;
}
.input_info.input_info--grey {
	font-family: $font_1;
	font-size: 2.8rem;
	font-weight: 400;
	line-height: 0.92;
	color: $color_descr_1;
	padding-top: 5.7rem;
}
.input_phone_wrapper.input_phone_wrapper--white {
	max-width: 101rem;
	background-color: $color_white;
}

.input_info_field_phone.input_info_field_phone--news::placeholder {
	font-family: $font_1;
	font-size: 2.8rem;
	font-style: italic;
	font-weight: 400;
	line-height: 0.92;
	color: #e3e3e3;
}

.btn_green.btn_green--square {
	max-height: 7.6rem;
	max-width: 24.3rem;
	border-radius: 0;
}
.main_news_callback {
	max-width: 108.1rem;
	display: flex;
	justify-content: start;
}
.phone_icon_wrapper.phone_icon_wrapper--white {
	border: none;
}

.input_phone_wrapper.input_phone_wrapper--news {
	min-height: 7.6rem;
	margin-bottom: 0;
	width: 97%;
}
