@use '../helpers/abstracts' as *;

.section_title.section_title--for_customer_reviews {
	position: relative;
	&::before {
		content: '';
		position: absolute;
		height: 0.4rem;
		background-color: $color_accent;
		@include bp($point_2, min) {
			left: -17rem;
			top: 3rem;
		}
		@include bp($point_3) {
			bottom: -2.2rem;
			left: calc(50% - 6rem);
		}
		@include bp($point_4, min) {
			width: 12rem;
		}
		@include bp($point_5) {
			width: 8.6rem;
			left: calc(50% - 4.3rem);
		}
	}
	@include bp($point_2, min) {
		margin-bottom: 3.4rem;
	}
	@include bp($point_3) {
		text-align: center;
		margin-bottom: 6rem;
	}
	@include bp($point_5) {
		max-width: 33rem;
		margin-inline: auto;
	}
}
.main_descr_1--for_customer_reviews {
	max-width: 70rem;
	@include bp($point_2, min) {
		margin-bottom: 4.2rem;
	}
	@include bp($point_3) {
		position: relative;
		left: 50%;
		transform: translateX(-50%);
		margin-bottom: 10rem;
		text-align: center;
	}
}
.customer_reviews {
	position: relative;
	width: 84.8rem;
	height: 55.3rem;

	@include bp($point_3) {
		width: 40.5rem;
		height: 130.2rem;
		left: 50%;
		transform: translateX(-50%);
		padding-bottom: 9.6rem;
	}
	@include bp($point_5) {
		width: 33.2rem;
		height: 132.6rem;
	}
}

.customer_reviews__swiper {
	width: 100%;
	height: 100%;
}

.swiper-slide {
	display: flex;
	column-gap: 4rem;
	padding-top: 3rem;
	@include bp($point_3) {
		flex-direction: column;
		row-gap: 4rem;
	}
}
.customer_reviews__element {
	position: relative;
	background-color: $color_white;
	width: 40.4rem;
	padding: 6.3rem 2rem 0 4.6rem;

	@include bp($point_5) {
		width: 33.2rem;
		padding: 7.3rem 2.8rem 0 2.8rem;
	}
	&::before {
		position: absolute;
		content: '“';
		left: 4.6rem;
		top: -8rem;
		font-family: $font_2;
		font-size: 20rem;
		font-weight: 400;
		color: $color_accent;
	}
}
.customer_reviews__descr {
	font-weight: 400;
	color: $color_descr_1;
	margin-bottom: 2.2rem;
	@include bp($point_4, min) {
		font-size: 2.2rem;
		line-height: 1.6;
	}
	@include bp($point_5) {
		font-size: 2rem;
		line-height: 1.7;
	}
}
.customer_reviews__client_name {
	font-size: 1.8rem;
	font-weight: 500;
	line-height: 1.9;
	padding-bottom: 4.6rem;
}
.prev,
.next {
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 6.2rem;
	height: 6.2rem;
	border-radius: 50%;
	background-color: $color_white;
	span {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 2.6rem;
		height: 1.8rem;
	}
}
.prev {
	@include bp($point_2, min) {
		left: -11.5rem;
		top: 22.2rem;
	}
	@include bp($point_3) {
		left: 0;
		bottom: 0rem;
	}
}
.next {
	@include bp($point_2, min) {
		right: -11.5rem;
		top: 22.2rem;
	}
	@include bp($point_3) {
		right: 0;
		bottom: 0rem;
	}
}
.section__bg_pic {
	@include bp($point_2, min) {
		background-image: url(.././imgs/customer_reviews/worker.png);
		background-repeat: no-repeat;
		background-position: right bottom;
		background-size: 54rem auto;
	}
}
