@use '../helpers/variables' as *;

.services_title {
	align-items: center;
	display: flex;
	flex-direction: column;
}

.services__title_decor {
	position: relative;
	margin-bottom: 8.6rem;
}

.services__title_decor::before {
	content: '';
	position: absolute;
	top: 8rem;
	left: 11rem;
	width: 12rem;
	height: 0.4rem;
	background-color: $color_accent;
}

.services_caption__decor::before {
	content: '';
	position: absolute;
	top: 19rem;
	left: -8rem;
	width: 0.4rem;
	height: 12rem;
	background-color: $color_accent;
}

.content_services {
	display: flex;
	justify-content: space-between;
	gap: 2rem;
}

.services_col__caption {
	flex: 1;
	display: flex;
	flex-direction: row-reverse;
	justify-content: flex-end;
	align-items: center;
	position: relative;
	text-transform: uppercase;
}

.services_caption {
	font-family: $font_1;
	font-size: 5.4rem;
	font-weight: 700;
	line-height: 6rem;
}

.services_col__image {
	flex: 1;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	overflow: hidden;
}

.services_picture {
	width: 43rem;
	height: 43rem;
	background-color: $color_section_bg;
}

.service__img {
	display: block;
	height: 100%;
	width: 100%;
	object-position: center;
	object-fit: contain;
}

.services__decor {
	position: relative;
	top: -0.5rem;
	left: 48.9rem;
	width: 7.1rem;
	height: 7.1rem;
	z-index: 1;
	pointer-events: none;
}

.services_col__text {
	flex: 1;
	padding: 2rem;
}

.services_col__title {
	font-family: $font_2;
	font-size: 3rem;
	font-weight: 700;
	line-height: 2.6rem;
	color: $color_text_2;
	margin-bottom: 2.8rem;
}

.services_col__subsrc {
	font-family: $font_1;
	font-size: 2rem;
	font-weight: 400;
	line-height: 3rem;
	color: $color_descr_1;
	padding-bottom: 3.5rem;
}

.swiper-our-services {
	position: relative;
	width: 100%;
	max-width: 100%;
	padding-bottom: 6.3rem;
}

.swiper-wrapper {
	display: flex;
}

.swiper-slide {
	width: 100%;
}

.swiper-pagination {
	position: absolute;
	bottom: 10px;
	left: 0;
	width: 100%;
	text-align: center;
	z-index: 10;
}

.swiper-pagination .swiper-pagination-bullet {
	width: 1.5rem;
	height: 1.6rem;
	background-color: #fff;
	border: 3px solid #d8d8d8;
	margin: 0 5px;
}

.swiper-pagination .swiper-pagination-bullet-active {
	background-color: $color_accent;
}