@use '../helpers/variables' as *;
@use '../helpers/mixins/breakpoints' as *;

.photo_gallery_title__decor {
	position: relative;
	padding-left: 17.3rem;
	margin-bottom: 3.6rem;

	@include bp($point_6) {
		font-size: 2.8rem;
		padding-left: 13.2rem;
	}

	@include bp($point_7) {
		font-size: 2.1rem;
		text-wrap: nowrap;
		padding-left: 11.2rem;

	}


	&:after {
		content: '';
		position: absolute;
		top: 3rem;
		left: 1rem;
		width: 12rem;
		height: 0.4rem;
		background-color: $color_accent;
		margin-right: 4.3rem;

		@include bp($point_4) {
			width: 86px;
			height: 4px;
			top: 1.5rem;
		}

	}
}



.photo_gallery__descr {
	text-align: center;
	text-align-last: left;
	padding-left: 17.3rem;

	font-family: $font_1;
	font-size: 2rem;
	font-weight: 400;
	line-height: 3rem;
	color: $color_descr_1;
	margin-bottom: 10rem;

	@include bp($point_4) {
		font-size: 1.6rem;
		line-height: 3rem;
		padding-left: 0;
		text-align: left;
	}

}

.img_wrapper {
	display: flex;
	flex-wrap: wrap;
	row-gap: 6rem;
	column-gap: 6rem;

	@include bp($point_4) {
		justify-content: center;
		align-items: center;
		display: flex;
		gap: 3rem;
	}

}

.img_wrapper_in {
	width: 43.3rem;
	height: 31.3rem;

	@include bp($point_4) {
		width: 36.7rem;
		height: 27.4rem;
	}

}

.photo_gallery__img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: center;
}