@use '../helpers/variables' as *;
@use '../helpers/extends/extends' as *;

.technique {
	display: flex;
	column-gap: 9.2rem;

	margin-bottom: 6rem;

	background-color: $color_white;

	@media (min-width: $point_2) {
		padding: 6rem 7rem;
	}

	@media (max-width: $point_3) {
		flex-direction: column;
		padding: 6rem 3rem;
	}
}

.technique__title {
	position: relative;
	color: $color_title;

	@media (min-width: $point_4) {
		padding-left: 17.3rem;
		margin-bottom: 8rem;
	}

	@media (max-width: $point_5) {
		padding-left: 10rem;
		margin-bottom: 4rem;
	}
}

.technique__title::before {
	content: '';
	position: absolute;
	left: 0rem;
	height: 0.4rem;
	background-color: $color_accent;
	margin-right: 4.3rem;

	@media (min-width: $point_4) {
		top: 3rem;
		width: 12rem;
	}

	@media (max-width: $point_5) {
		top: 1.6rem;
		width: 8.6rem;
	}
}

.technique__info {
	@media (min-width: $point_2) {
		width: calc(56rem / (142rem - 14rem - 9.2rem) * 100%);
	}

	@media (max-width: $point_3) {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: auto;
		order: 2;
	}
}

.technique__info_title {
	@media (min-width: $point_2) {
		font-family: $font_2;
		font-size: 2.6rem;
		font-weight: 700;
		line-height: 1;
		color: $color_title;
		margin-bottom: 3.8rem;
	}

	@media (max-width: $point_3) {
		display: none;
	}
}

.technique__info_title_m {
	@media (min-width: $point_2) {
		display: none;
	}

	@media (max-width: $point_3) {
		display: flex;
		justify-content: center;
		font-family: $font_2;
		font-size: 3.6rem;
		font-weight: 700;
		line-height: 1;
		color: $color_title;
		margin-bottom: 3.8rem;
		order: 0;
	}
}

.technique__info_descr {
	font-family: $font_1;
	font-size: 2rem;
	line-height: 1.5;
	color: $color_descr_1;
	margin-bottom: 3rem;

	@media (max-width: $point_3) {
		text-align: center;
	}
}

.technique__info_weight {
	font-family: $font_1;
	font-size: 2rem;
	line-height: 1.5;
	color: $color_text_2;
	margin-bottom: 7.2rem;
}

.technique__info_bottom {
	display: flex;
	column-gap: 8.6rem;
}

.technique__info_bottom_price {
	display: flex;
	align-items: center;
	font-family: $font_1;
	font-size: 4rem;
	font-weight: 700;
	line-height: 1;
	color: $color_text_2;

	@media (min-width: $point_2) {
		flex-wrap: wrap;
	}

	@media (max-width: $point_3) {
		gap: 1rem;
		flex-direction: column;
	}

	& span {
		font-size: 1.6rem;
		font-weight: 500;
		line-height: 1.9;
	}
}

.technique__photo-bg {
	position: absolute;
	top: 0;
	left: 14.6rem;
	width: 40rem;
	height: 40rem;
	background-color: #f0f0f0;
	display: flex;
	justify-content: center;
	align-items: center;
}

.technique__photo {
	position: relative;
	display: block;
	height: 39.4rem;
	order: 1;

	@media (min-width: $point_2) {
		width: calc(62.2rem / (142rem - 14rem - 9.2rem) * 100%);
	}

	@media (max-width: $point_3) {
		width: 62rem;
		margin: 0 auto;
		margin-bottom: 4rem;
	}
}

.technique__photo_img {
	position: relative;
	@extend %cover_img;
	z-index: 1;
}
