@use '../helpers/abstracts' as *;

.section_title__additional_products {
	position: relative;
	left: 16.3rem;
	margin-bottom: 2.9rem;
	&::before {
		content: '';
		position: absolute;
		width: 12rem;
		height: 4px;
		left: -16.3rem;
		bottom: 2.7rem;
		background-color: $color_accent;
	}
}
.bace_text__additional_products {
	margin-bottom: 10rem;
}
.additional_products {
	position: relative;
	width: 100%;
	display: flex;
	column-gap: 2rem;
	align-items: center;
	z-index: 1;
	&::after {
		position: absolute;
		content: url(.././imgs/bestseller/dots.svg);
		width: 7.1rem;
		height: 7.1rem;
		z-index: -1;
		right: -3.6rem;
		top: -3.6rem;
	}
	&::before {
		position: absolute;
		content: url(.././imgs/bestseller/dots.svg);
		width: 7.1rem;
		height: 7.1rem;
		z-index: -1;
		left: -3.6rem;
		bottom: -3.6rem;
	}
}
.additional_products__block {
	width: calc(
		100% / $colums_number_product - $colums_gap_product * ($colums_number_product - 1) / $colums_number_product
	);
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	padding-top: 4.4rem;
	padding-bottom: 3.6rem;
	border: 1px solid $color_section_bg;
	box-shadow: 0 4px 2rem 0 $color_shadow;
	background-color: $color_white;
}

.additional_products__pic {
	display: block;
	width: 28.5rem;
	height: 20rem;
	margin-bottom: 6.1rem;
	&.additional_products__pic--high {
		width: 26rem;
		height: 22.5rem;
		margin-bottom: 3.2rem;
	}
}
.additional_products__img {
	@extend %contain_img;
}
.additional_products__title {
	font-family: $font_2;
	font-size: 2.6rem;
	font-weight: 700;
	line-height: 1;
	margin-bottom: 1.6rem;
}
.additional_products__colors {
	display: flex;
	align-items: center;
	justify-content: center;
	column-gap: 2.2rem;
	margin-bottom: 2.4rem;
}
.additional_products__color_circle {
	width: 2.5rem;
	height: 2.5rem;
	border-radius: 50%;
	&.additional_products__color_circle--grey {
		background-color: $color_descr_1;
	}
	&.additional_products__color_circle--red {
		background-color: $color_red;
	}
	&.additional_products__color_circle--brown {
		background-color: $color_brown;
	}
	&.additional_products__color_circle--black {
		background-color: $color_black;
	}
	&.additional_products__color_circle--yellow {
		background-color: $color_yellow;
	}
}
.additional_products__info {
	font-family: $font_1;
	text-align: center;
	font-size: 1.6rem;
	font-weight: 400;
	line-height: 1.5;
	margin-bottom: 0.8rem;
	&.additional_products__info--mar {
		margin-bottom: 3rem;
	}
	.additional_products_span {
		font-size: 2.5rem;
		font-weight: 500;
		line-height: 1;
	}
}
