@use '../helpers/variables' as *;
@use '../helpers/extends/extends' as *;

.info {
	display: flex;
}

.info__text {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	gap: 1px;
	width: 50%;
	background-color: #e1e1e1;
}

.info__text_block {
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
	min-height: 30rem;

	padding: 4rem 3.6rem 0 3.6rem;
	background-color: $color_section_bg;
}

.info__text_icon {
	background-color: $color_accent;
	border-radius: 50%;
	padding: 1.2rem 1.2rem 1.2rem 1.2rem;
	margin-bottom: 0.8rem;
}

.info__text_img {
	display: block;
	width: 6rem;
	height: 6rem;
}

.info__text_image {
	@extend %contain_img;
}

.info__img {
	width: 50%;
}

.info__image_main {
	display: block;
	width: 100%;
	height: 100%;
	background-image: url(../imgs/info/info_main.jpg);
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;

	background-attachment: fixed; // parallax
}
