.porch {
	max-width: 133.3rem;
	column-gap: 27.1rem;
}
.porch__pic {
	display: block;
	position: relative;
	z-index: 1;
	@include bp($point_3) {
		margin-bottom: 8rem;
	}
	@include bp($point_4, min) {
		padding-top: 4.6rem;
		width: 46rem;
		height: 50.6rem;
	}
	@include bp($point_5) {
		width: 26.6rem;
		height: 26.6rem;
	}
	.porcht__img {
		display: block;
		width: 100%;
		height: 100%;
		object-position: left center;
		object-fit: cover;
	}
	&::before {
		content: '';
		position: absolute;
		z-index: -1;
		background-color: $color_accent;
		@include bp($point_4, min) {
			top: 0;
			left: -4.6rem;
			width: 9.2em;
			height: 9.2em;
		}
		@include bp($point_5) {
			top: -2.6rem;
			left: -2.3rem;
			width: 5.4rem;
			height: 5.4rem;
		}
	}
	&::after {
		content: '';
		position: absolute;
		z-index: -1;
		background-image: url(.././imgs/why_we/pic_decor.svg);
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		@include bp($point_2, min) {
			right: -7.8em;
		}
		@include bp($point_4, min) {
			top: 18em;
			right: -4.5rem;
			width: 29.2rem;
			height: 38.4rem;
		}
		@include bp($point_5) {
			bottom: -4.4rem;
			right: -4.4rem;
			width: 16.7rem;
			height: 22rem;
		}
	}
}
.porch__title {
	position: relative;

	&::after {
		content: '';
		position: absolute;
		height: 0.4rem;
		background-color: $color_accent;
		@include bp($point_2, min) {
			left: -17rem;
			top: 3rem;
		}
		@include bp($point_3) {
			bottom: -2.2rem;
			left: calc(50% - 6rem);
		}
		@include bp($point_4, min) {
			width: 12rem;
		}
		@include bp($point_5) {
			width: 8.6rem;
			left: calc(50% - 4.3rem);
		}
	}
	@include bp($point_2, min) {
		margin-bottom: 3.6rem;
	}
	@include bp($point_4, min) {
		margin-bottom: 4.2rem;
	}
	@include bp($point_5) {
		margin-bottom: 8.8rem;
		max-width: 19.2rem;
		text-align: center;
	}
}

.porch__text {
	max-width: 59.8rem;
	margin-bottom: 4.1rem;
}
.btn_green {
	@include bp($point_4, min) {
		min-width: 28.2rem;
	}
	@include bp($point_5) {
		min-width: 33.7rem;
	}
}
